#keyboard {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 20px;
    width: 100%;
    height: 200px;
}

.keys {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
}

.upper-keys {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}

.lower-keys {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}

.key {
    border-left: 1px solid black;
}

.key-out-of-range {
    opacity: 0.25;
}

.white-key {
    background-color: white;
}

.black-key {
    background-color: #222;
    border-bottom: 1px solid black;
}

.in-current-key.take-playing {
    background-color: coral;
}

.in-current-key.take-playing.depressed, .depressed {
    background: lawngreen;
}

h2 {
    -ms-flex-item-align: center;
        align-self: center;
}



